"use client";

import { Fixture } from "@/types";
import { useState } from "react";
import FixtureScoreModal from "./FixtureScoreModal";

interface UpcomingFixturesProps {
  fixtures: Fixture[];
  onAddFixture: (fixture: Fixture) => void;
  addedFixtureIds: Set<string>;
}

export default function UpcomingFixtures({
  fixtures,
  onAddFixture,
  addedFixtureIds,
}: UpcomingFixturesProps) {
  const [selectedFixture, setSelectedFixture] = useState<Fixture | null>(null);

  const handleFixtureClick = (fixture: Fixture) => {
    // If fixture already has scores (completed match), add directly
    if (fixture.homeScore !== null && fixture.awayScore !== null) {
      onAddFixture(fixture);
    } else {
      // Otherwise, show modal to enter predicted scores
      setSelectedFixture(fixture);
    }
  };

  const formatDate = (dateString: string | undefined) => {
    if (!dateString) return "";
    const date = new Date(dateString);
    return date.toLocaleDateString("en-US", {
      month: "short",
      day: "numeric",
      year: "numeric",
    });
  };

  const formatTime = (dateString: string | undefined) => {
    if (!dateString) return "";
    const date = new Date(dateString);
    return date.toLocaleTimeString("en-US", {
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  if (fixtures.length === 0) {
    return (
      <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6 border border-gray-200 dark:border-gray-700">
        <h2 className="text-2xl font-bold mb-4 text-gray-800 dark:text-gray-100">
          Upcoming Fixtures
        </h2>
        <p className="text-gray-500 dark:text-gray-400 text-center py-8">
          No upcoming fixtures available.
        </p>
      </div>
    );
  }

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-lg p-6 border border-gray-200 dark:border-gray-700">
      <h2 className="text-2xl font-bold mb-4 text-gray-800 dark:text-gray-100">
        Upcoming Fixtures ({fixtures.length})
      </h2>
      <div className="space-y-2 max-h-96 overflow-y-auto">
        {fixtures.map((fixture) => {
          const isAdded = addedFixtureIds.has(fixture.id);
          const hasScore =
            fixture.homeScore !== null && fixture.awayScore !== null;

          return (
            <div
              key={fixture.id}
              className={`flex items-center justify-between p-3 rounded-lg border transition-all ${
                isAdded
                  ? "bg-green-50 dark:bg-green-900/20 border-green-300 dark:border-green-700"
                  : "bg-gray-50 dark:bg-gray-900 border-gray-200 dark:border-gray-700 hover:border-green-400"
              }`}
            >
              <div className="flex-1 min-w-0">
                <div className="flex items-center justify-between gap-2 text-sm mb-1">
                  <div className="flex items-center gap-2">
                    <span className="text-gray-600 dark:text-gray-400">
                      {formatDate(fixture.date)}
                    </span>
                    <span className="text-gray-400 dark:text-gray-500">
                      {formatTime(fixture.date)}
                    </span>
                  </div>
                  <div className="flex gap-1">
                    {fixture.isRwc && (
                      <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-yellow-100 dark:bg-yellow-900 text-yellow-800 dark:text-yellow-200">
                        RWC
                      </span>
                    )}
                    {hasScore && (
                      <span className="inline-flex items-center px-2 py-0.5 rounded text-xs font-medium bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200">
                        Completed
                      </span>
                    )}
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <div className="flex-1 text-right">
                    <span className="font-medium text-gray-900 dark:text-gray-100 text-sm">
                      {fixture.homeTeam.name}
                    </span>
                  </div>
                  {hasScore ? (
                    <div className="flex items-center gap-2 bg-white dark:bg-gray-800 px-3 py-1 rounded shadow-sm">
                      <span className="font-bold text-gray-900 dark:text-gray-100">
                        {fixture.homeScore}
                      </span>
                      <span className="text-gray-400">-</span>
                      <span className="font-bold text-gray-900 dark:text-gray-100">
                        {fixture.awayScore}
                      </span>
                    </div>
                  ) : (
                    <div className="text-gray-400 text-sm px-3">vs</div>
                  )}
                  <div className="flex-1">
                    <span className="font-medium text-gray-900 dark:text-gray-100 text-sm">
                      {fixture.awayTeam.name}
                    </span>
                  </div>
                </div>
              </div>
              <button
                onClick={() => handleFixtureClick(fixture)}
                disabled={isAdded}
                className={`ml-3 p-2 rounded-lg transition-colors ${
                  isAdded
                    ? "text-green-600 dark:text-green-400 cursor-not-allowed opacity-50"
                    : "text-green-600 dark:text-green-400 hover:bg-green-100 dark:hover:bg-green-900/30"
                }`}
                title={isAdded ? "Already added" : "Add to predictions"}
              >
                {isAdded ? (
                  <svg
                    className="w-5 h-5"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path
                      fillRule="evenodd"
                      d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                ) : (
                  <svg
                    className="w-5 h-5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M12 4v16m8-8H4"
                    />
                  </svg>
                )}
              </button>
            </div>
          );
        })}
      </div>

      {/* Score Modal */}
      {selectedFixture && (
        <FixtureScoreModal
          fixture={selectedFixture}
          onClose={() => setSelectedFixture(null)}
          onSubmit={(fixture) => {
            onAddFixture(fixture);
            setSelectedFixture(null);
          }}
        />
      )}
    </div>
  );
}
